@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Courier New", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f1f1f1;
  color: rgba(0, 0, 0, 0.58) !important;
  text-decoration: none;
  font-family: SoDoSans, Helvetica Neue, Helvetica, Arial, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
    max-width:70%;
    height: 70%;
}

.shrink{
    margin-left: 0;
    margin-top: -15px;
    /* margin-top: 25px; */
    max-width:80%;
    height:80%;
}
.NavbarItems {
  /* background-color: linear-gradient(90deg, rgb(110,94,254) 0%, rgba(73,63,252, 100%)); */
  height: 60px;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  font-size: 1.2 rem;
}

.navbar-logo {
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
}

.rightButtons {
  margin-left: auto;
  margin-right: 25px;
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
}

a {
  color: #212121;
  text-decoration: none;
  font-size: 16px;
}

.slogan {
  position: static;
  /* margin: 30px; */
  margin-left: 1px;
  margin-bottom: 20px;
}

.slogan h1 {
  color: rgb(0, 117, 74);
  font-weight: 600;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #96ccff;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.bta {
  background: none;
  border-radius: 50px;
  border-style: solid;
  border-width: 1px;
  font-weight: 600;
  line-height: 1.2;
  padding: 7px 16px;
  text-align: center;
  text-decoration: none;
  transition: all 0.2s ease;
  background-color: #000;
  border-color: #000;
  color: #fff;
}

.drink-container {
  width: 100%;
  margin: auto;
  padding: 10px 0px 10px 35px;
  position: relative;
  height: auto;
  float: right;
  align-content: space-evenly;
}

body {
  background: #f2f2f2;
  font-family: "Open Sans", sans-serif;
}

.search {
  width: 100%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 3px solid rgb(0, 117, 74);
  padding: 5px;
  height: 36px;
  border-radius: 5px;
  outline: none;
  color: #666;
}

.searchTerm:focus {
  color: #212121;
}

/*Resize the wrap to see the search bar change!*/
.wrap {
  width: 350px;
  position: relative;
  top: 4px;
  float: right;
  margin-right: 100px;
}

.drinkListContainer {
  padding: 10px 22px;
}

.line {
  border-top: 3px solid rgb(0, 117, 74);
  padding: 0px 22px;
  width: 100px;
  margin: -10px 0px 20px 20px;
}


.heightCard300{
  height: 300px;
}

.widthSpaced31{
  width: 31%;
}
.drinkImg {
  /* float: left; */
  text-align: left;
  max-width: 100%;
  max-height: 100%;
  margin-left: 50px;
  /* margin-top: 50px; */
}

.centerHeader {
  text-align: center;
}

.floatRight {
  float: right;
  margin-left: 100px;
}

.space100 {
  margin-left: 100px;
}

.reviewRectangle {
  width: 70;
  height: auto;
  border-bottom: 1px solid #919191;
  padding-bottom: 40px !important;
  /* margin-left: 100px; */
  margin-bottom: 30px;

  font-family: Arial;
  font-weight: bold;
}


.marginleft20{
  margin-left: 20px;
}

.mousepointer {
  cursor: pointer;
}

.left {
  width: 20%;
}

.right {
  width: 75%;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

.column {
  float: left;
}

.drinkpage-list {
  display: flex;
  align-content: space-evenly;
  flex-flow: wrap;
  height: 100px;
}

.drinkpage-list li {
  min-width: 200px;
  list-style: revert;
}


.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: rgba(0, 0, 0, 0.58);
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #00754a;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

#submitted {
  color : #00754a
}

